.stat {
	margin-top: 20px;
	text-align: center;
}

.stat .block {
	display: inline-block;
	width: 45%;
	vertical-align: top;
	margin-bottom: 30px;
}

.stat .block .name {
	display: block;
	height: 35px;
}

.stat .block .digit {
	display: block;
	padding-top: 5px;
	padding-bottom: 0;
	font-size: 35px;
}

.stat .block .description {
	display: block;
}

.List.harm span.digit {
	font-size: 22px;
	display: block;
    margin: 5px 0;
}

.List.harm span.description {
	display: block;
	white-space: normal;
}

.Div.about {
	text-align: center;
	margin: 0 auto;
}

.Div.about img.app-icon {
    border-radius: 20px;
	width: 70px;
	height: 70px;
	background: #fff;
}

.Div.about .app-name {
	display: block;
}

.Div.about .app-version {
	display: block;
}

.Div.about .app-company {
	display: block;
}

.Cell.set-actual-datetime {
	color: #528bcc;
	color: var(--accent);
}

.Cell.set-actual-datetime .Icon {
	margin-top: -15px;
	display: inline-block;
	vertical-align: -3.5px;
}

.set-actual-datetime {
	color: #528bcc;
	color: var(--accent);
}


.List.health span.title {
	font-size: 22px;
	display: block;
    margin: 5px 0;
}

.List.health span.text {
	display: block;
	white-space: normal;
}

.List.health span.progress-text {
	padding-top: 10px;
	display: block;
	white-space: normal;
}
